import React from "react";
import "./counter.css";

function Counter (props) {
    let count = 0;
    let counterPlus = document.querySelector("#add")
    let counterMinus = document.querySelector("#minus")
    let counter = document.querySelector(".counter-display")

    function updateDisplay () {
        counter.innerHTML = count;
    }

    updateDisplay();
    
    counterPlus.addEventListener("click",()=> {
        counter+= 1;
        updateDisplay();
    })
    counterMinus.addEventListener("click",()=> {
        if(counter > 0) {
            counter-= 1;
            updateDisplay();
        }
    })
    return(
        <div>
            <button className="counter" id="minus">-</button>
            <p className="counter-display"></p>
            <button className="counter" id="add">+</button>
        </div>
    )
}


export default Counter;